import isNumber from 'lodash/isNumber';
import isString from 'lodash/isString';
import { logException } from '../log/logException';

/**
 * 分--->元
 * @param price 价格，单位为分
 * @param allowZero
 * @returns 分变成元之后的价格
 */
export function parsePriceFromFen2Yuan(
    price: number | string,
    allowZero: Boolean,
): string {
    let priceNum;
    if (isString(price)) {
        try {
            priceNum = parseFloat(price.toString());
        } catch {
            logException('parseFloat', '字符串转换数字异常');
        }
    } else {
        priceNum = price;
    }
    if (isNumber(priceNum) && priceNum >= 0) {
        if (!allowZero && priceNum === 0) {
            return undefined;
        }
        const priceYuan = priceNum / 100;
        return parseFloat(priceYuan.toString()).toString();
    }
    return undefined;
}
